@import url(http://fonts.cdnfonts.com/css/lilly);
@import url(https://fonts.googleapis.com/css2?family=Oswald&display=swap);
.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  grid-gap: 50px;
  gap: 50px;
  padding-top: 30px;
}
h1 {
  font-family: "Oswald", sans-serif;
  font-size: 50px;
}
.gen input {
  height: 35px;
  width: 250px;
  font-size: 20px;
  padding-left: 5px;
}
button {
  position: relative;
  height: 38px;
  width: 100px;
  top: -2px;
  font-size: 18px;
  border: none;
  color: whitesmoke;
  background-color: forestgreen;
  box-shadow: 2px 2px 5px rgb(74, 182, 74);
  cursor: pointer;
}
button:active {
  box-shadow: none;
}
.extra {
  padding-top: 20px;
  display: flex;
  justify-content: space-around;
  grid-gap: 10px;
  gap: 10px;
}
.output-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 40px;
  gap: 40px;
}

